aside.menu-area {
    background-color: var(--bg-dark);
    box-shadow: 
        2px 0 10px 0 rgba(0, 0, 0, 0.12),
        2px 0 15px 0 rgba(0, 0, 0, 0.09);
}


.menu a {
     display: block;
     text-decoration: none;
     font-weight: 300;
     color: #FFF;
     padding: 15px;
}

.menu a:hover {
    background: linear-gradient(135deg, #07a7e3, #32dac3)
}

@media(max-width: 768px) {
    .menu {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }

    .menu a {
         display: inline;
         padding: 10px;
         margin: 0px;
    }

    .menu {
         height: 100%;
         justify-content: space-around;
    }
}